import { GameState } from "othello-rs";
import { AgentWorkerReadyMessage, AgentWorkerStartMessage } from "./AgentWorkerMessage";

function handleMessage(event: MessageEvent) {
  const message: AgentWorkerStartMessage = event.data;
  if (message.type !== 'start') {
    console.error('Unexpected message type:', message.type);
    return;
  }
  switch (message.agentSettings.type) {
    case 'random': {
      const gameState = GameState.from_js_value(JSON.parse(message.gameStateString));
      const actions = gameState.actions_vec();
      const playerAction = actions[Math.floor(Math.random() * actions.length)];
      const response: AgentWorkerReadyMessage = {
        type: 'ready',
        playerActionId: message.playerActionId,
        playerActionString: JSON.stringify(playerAction.to_js_value()),
      };
      /* eslint-disable-next-line no-restricted-globals */
      self.postMessage(response);
      break;
    }
    default:
      console.error('Unexpected agent type:', message.agentSettings.type);
  }
}

/* eslint-disable-next-line no-restricted-globals */
self.onmessage = handleMessage;
